<template>
    <v-row>
        <v-col cols="12">
            <v-color-picker
                v-model="value"
                hide-inputs
                elevation="0"
                width="100%"
            ></v-color-picker>
        </v-col>
    </v-row>
    <v-row>
        <v-col cols="4" v-for="(p, i) in palette" :key="i">
            <v-btn width="100%" :color="p" @click="value = p"></v-btn>
        </v-col>
    </v-row>
</template>

<script>
export default {
    name: "ColorSelector",
    props: ["modelValue"],
    computed: {
        value: {
            get() {
                return this.modelValue;
            },
            set(value) {
                this.$emit("update:modelValue", value);
            },
        },
    },
    data() {
        return {
            palette: {
                primary: "#1976D2",
                secondary: "#03DAC6",
                success: "#4CAF50",
                warning: "#FB8C00",
                error: "#B00020",
                purple: "#9134aa",
            },
        };
    },
    mounted() {
        if (!this.value) {
            this.value = this.palette.primary;
        }
    },
};
</script>
