<template>
    <v-card>
        <v-toolbar density="compact" color="primary">
            <v-btn icon @click="$emit('close')">
                <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title>設定</v-toolbar-title>
            <v-spacer></v-spacer>
            <!-- <v-toolbar-items> -->
            <v-btn @click="$emit('close')">
                <v-icon start icon="mdi-content-save-outline"></v-icon
                >保存</v-btn
            >
            <!-- </v-toolbar-items> -->
        </v-toolbar>
        <v-list lines="two" subheader>
            <v-list-subheader>マスタメンテ</v-list-subheader>
            <v-list-item
                title="お店マスタのメンテ"
                subtitle="お店マスタのメンテナンスをします"
            ></v-list-item>
            <v-container>
                <v-row>
                    <v-col cols="12" md="6">
                        <v-container>
                            <v-row>
                                <v-col cols="12">
                                    <v-text-field
                                        v-model="shop_name"
                                        label="お店"
                                        variant="outlined"
                                        clearable
                                    ></v-text-field>
                                </v-col>
                            </v-row>
                            <v-card class="pb-4 px-2">
                                <v-card-title>カラー</v-card-title>
                                <v-row>
                                    <v-col cols="12">
                                        <color-selector
                                            v-model="picker"
                                        ></color-selector>
                                    </v-col>
                                </v-row>
                            </v-card> </v-container
                    ></v-col>
                    <v-col cols="12" md="6">
                        <v-container>
                            <v-row>
                                <v-col cols="12">
                                    <v-card>
                                        <v-card-title>
                                            登録済みお店リスト</v-card-title
                                        >
                                        <v-list>
                                            <v-list-item
                                                v-for="(item, i) in shop_list"
                                                :key="i"
                                                :title="item.name"
                                                :color="item.color"
                                            >
                                                <template v-slot:prepend>
                                                    <v-icon
                                                        icon="mdi-circle"
                                                        :color="item.color"
                                                    ></v-icon>
                                                </template>
                                                <template v-slot:append>
                                                    <v-icon
                                                        v-show="i != 0"
                                                        icon="mdi-arrow-up-thin"
                                                        @click="up(i)"
                                                        class="me-2"
                                                    ></v-icon>
                                                    <v-checkbox
                                                        v-model="item.dispGraph"
                                                        color="primary"
                                                        hide-details
                                                    ></v-checkbox>
                                                    <v-icon
                                                        icon="mdi-trash-can-outline"
                                                        @click="deleteShop(i)"
                                                    ></v-icon> </template
                                            ></v-list-item>
                                        </v-list>
                                    </v-card>
                                </v-col>
                            </v-row>
                        </v-container>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col cols="12">
                        <v-btn color="primary" @click="addShop">お店追加</v-btn>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col cols="12">
                        <confirm-dialog @ok="updateShop">
                            <template #opener="{ props }">
                                <v-btn v-bind="props" color="warning"
                                    >お店更新</v-btn
                                >
                            </template>
                            <template #message>お店リストを更新します</template>
                        </confirm-dialog>
                    </v-col>
                </v-row>
            </v-container>
            <v-divider></v-divider>
            <v-list-subheader>データの操作</v-list-subheader>
            <v-list-item
                title="データの出力"
                subtitle="データベースのデータをJSON形式で出力します"
            ></v-list-item>
            <v-container>
                <v-row>
                    <v-col cols="12">
                        <v-btn color="primary" @click="outputDB"
                            >データ出力</v-btn
                        >
                    </v-col>
                </v-row>
            </v-container>
            <v-list-item
                title="データの入力"
                subtitle="JSON形式のファイルをデータベースに入力します"
            ></v-list-item>
            <v-container>
                <v-row>
                    <v-col cols="12">
                        <v-file-input
                            v-model="inputFile"
                            label="入力データJson"
                            accept="application/json"
                            show-size
                        ></v-file-input>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col cols="12">
                        <confirm-dialog @ok="inputDB">
                            <template #opener="{ props }">
                                <v-btn v-bind="props" color="warning"
                                    >データ入力</v-btn
                                >
                            </template>
                            <template #message>データを入力します</template>
                        </confirm-dialog>
                    </v-col>
                </v-row>
            </v-container>
            <v-list-item
                title="データパッチ"
                subtitle="データベースにパッチをあてます"
            ></v-list-item>
            <v-container>
                <v-row>
                    <v-col cols="12">
                        <confirm-dialog @ok="patchDB">
                            <template #opener="{ props }">
                                <v-btn
                                    v-bind="props"
                                    color="warning"
                                    @click="patchDB"
                                    >データパッチ</v-btn
                                >
                            </template>
                            <template #message
                                >データにパッチを当てます</template
                            >
                        </confirm-dialog>
                    </v-col>
                </v-row>
            </v-container>
            <v-divider></v-divider>
            <v-list-subheader>データの消去</v-list-subheader>
            <v-list-item
                title="データの消去"
                subtitle="データベースのデータを消去します"
            ></v-list-item>
            <v-container>
                <v-row>
                    <v-col cols="12">
                        <confirm-dialog @ok="eraseDB">
                            <template #opener="{ props }">
                                <v-btn v-bind="props" color="warning"
                                    >データ消去</v-btn
                                >
                            </template>
                            <template #message>データを消去します</template>
                        </confirm-dialog>
                    </v-col>
                </v-row>
            </v-container>
        </v-list>
    </v-card>
</template>

<script>
import saveAs from "file-saver";
import ColorSelector from "../common/ColorSelector.vue";
import ConfirmDialog from "../common/ConfirmDialog.vue";

export default {
    name: "AppSettings",
    components: { ConfirmDialog, ColorSelector },
    data: () => ({
        inputFile: null,
        shop_name: "",
        picker: null,
        shop_list: [],
    }),
    mounted() {
        this.shop_list = this.getShopList();
    },
    methods: {
        outputDB() {
            const outputJson = this.getEventData();
            const output = JSON.stringify(outputJson);

            const blob = new Blob([output], {
                type: "application/json",
            });

            saveAs(blob, "izakaya.json");
        },
        async inputDB() {
            const data = await this.readInputFile();

            var inputJson = JSON.parse(data);
            this.setEventData(inputJson);
            this.$store.dispatch("increment");
            this.shop_list = this.getShopList();
        },
        readInputFile() {
            return new Promise((resolve, reject) => {
                const reader = new FileReader();
                reader.onload = () => {
                    resolve(reader.result);
                };
                reader.onerror = reject;
                reader.readAsText(this.inputFile[0]);
            });
        },
        patchDB() {
            const eventData = this.getEventData();
            eventData.list.forEach((t) => {
                // t.price = Number(t.price);
                t.dispGraph = true;
            });
            this.setEventData(eventData);
            this.$store.dispatch("increment");
        },
        eraseDB() {
            this.setEventData({});
            this.$store.dispatch("increment");
            this.shop_list = this.getShopList();
        },
        addShop() {
            this.shop_list.push({
                name: this.shop_name,
                dispGraph: true,
                color: this.picker,
            });
        },
        up(i) {
            if (i == 0) {
                return;
            }
            [this.shop_list[i - 1], this.shop_list[i]] = [
                this.shop_list[i],
                this.shop_list[i - 1],
            ];
        },
        updateShop() {
            this.updateShopList(this.shop_list);
            this.$store.dispatch("increment");
        },
        deleteShop(i) {
            if (this.shop_list.length == 1) {
                this.shop_list = [];
            } else {
                this.shop_list.splice(i, 1);
            }
            this.$store.dispatch("increment");
        },
    },
};
</script>
