<template>
    <v-container>
        <v-timeline side="end" align="start">
            <v-timeline-item
                size="small"
                :dot-color="item.event_color"
                v-for="(item, i) in data"
                :key="i"
            >
                <template v-slot:opposite>
                    <span>{{ item.event_date }}</span>
                </template>
                <div>
                    <h2 :class="`mt-n1 headline font-weight-light mb-4`">
                        {{ item.shop_name }}
                    </h2>
                    <h4 :class="`mt-n1 ml-4 headline font-weight-light mb-4`">
                        {{ item.comment }}
                    </h4>
                    <div>{{ item.price.toLocaleString() }}円</div>
                    <v-dialog v-model="update_event_dialog[i]" width="80%">
                        <template #activator="{ props }">
                            <v-btn v-bind="props" color="primary"
                                ><v-icon
                                    start
                                    icon="mdi-square-edit-outline"
                                ></v-icon
                                >編集</v-btn
                            >

                            <!-- <v-btn
                        v-if="$vuetify.display.xs"
                        class="ml2"
                        v-bind="props"
                    >
                        <v-icon icon="mdi-pencil-plus-outline"></v-icon>
                    </v-btn>
                    <v-btn class="ml2" v-bind="props">
                        <v-icon start icon="mdi-pencil-plus-outline"></v-icon
                        ><span>記録追加</span>
                    </v-btn> -->
                        </template>
                        <new-event
                            :mode="1"
                            @close="update_event_dialog[i] = false"
                            :event="item"
                        />
                    </v-dialog>

                    <v-btn
                        @click="onClickDeleteEvent(item)"
                        color="warning"
                        class="ml-2"
                        ><v-icon start icon="mdi-trash-can-outline"></v-icon
                        >削除</v-btn
                    >
                </div>
            </v-timeline-item>
        </v-timeline>
    </v-container>
</template>

<script>
import NewEvent from "./NewEvent.vue";

export default {
    name: "TimelineView",
    components: {
        NewEvent,
    },
    computed: {
        count() {
            return this.$store.getters.getCount;
        },
    },
    watch: {
        count() {
            console.log("refresh!!");
            this.data = this.getEventList();
            // this.initDialogFlg();
        },
    },
    data: () => ({
        data: [],
        update_event_dialog: [],
    }),
    mounted() {
        this.data = this.getEventList();
        this.initDialogFlg();
    },
    methods: {
        initDialogFlg() {
            this.update_event_dialog = [...Array(this.data.length)].map(
                () => false
            );
        },
        onClickDeleteEvent(e) {
            this.deleteEvent(e);
            this.$store.dispatch("increment");
        },
        onClickEditEvent(e) {
            this.updateEvent(e);
            this.$store.dispatch("increment");
        },
    },
};
</script>
