<template>
    <Bar :data="data" :options="options" />
</template>

<script>
import {
    BarElement,
    CategoryScale,
    Chart as ChartJS,
    Legend,
    LinearScale,
    Title,
    Tooltip,
} from "chart.js";
import { Bar } from "vue-chartjs";

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
);

// [
//     {
//         label: "",
//         color: "",
//         data: 0
//     }
// ]

export default {
    name: "BarView",
    components: {
        Bar,
    },
    props: ["graphData"],
    data() {
        return {
            data: {
                labels: this.graphData.map((d) => d.label),
                datasets: [
                    {
                        backgroundColor: this.graphData.map((d) => d.color),
                        data: this.graphData.map((d) => d.data),
                    },
                ],
            },
            options: {
                responsive: true,
                maintainAspectRatio: false,
            },
        };
    },
};
</script>
