<template>
    <!-- usage
    <confirm-dialog @ok="updateShop">
        <template #opener="{ props }">
            <v-btn v-bind="props" color="warning">お店更新</v-btn>
        </template>
        <template #message>お店リストを更新します</template>
    </confirm-dialog> -->

    <v-dialog v-model="isShow" persistent>
        <template #activator="{ props }">
            <slot name="opener" :props="props"></slot>
        </template>

        <v-sheet>
            <v-sheet class="my-2 mx-5">
                <h2><slot name="title">確認</slot></h2>
                <v-divider></v-divider>
                <p class="my-4"><slot name="message">よろしいですか？</slot></p>

                <div class="d-flex justify-end my-2">
                    <v-btn class="mx-2" color="primary" @click="onOk"
                        ><slot name="posi">OK</slot></v-btn
                    >
                    <v-btn class="mx-2" color="error" @click="onCancel"
                        ><slot name="nega">Cancel</slot></v-btn
                    >
                </div>
            </v-sheet>
        </v-sheet>
    </v-dialog>
</template>

<script>
export default {
    name: "ConfirmDialog",
    data() {
        return {
            isShow: false,
        };
    },
    methods: {
        onOk() {
            this.$emit("ok");
            this.isShow = false;
        },
        onCancel() {
            this.$emit("cancel");
            this.isShow = false;
        },
    },
};
</script>
