<template>
    <v-container>
        <v-row>
            <v-col cols="12" sm="6">
                <v-card height="100%" max-height="320px">
                    <Bar :graphData="data"></Bar>
                </v-card>
            </v-col>
            <v-col cols="12" sm="6">
                <v-card height="100%" max-height="320px">
                    <Doughnut :graphData="data"></Doughnut>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import Bar from "../common/chart/Bar";
import Doughnut from "../common/chart/Doughnut";

export default {
    name: "DashBoard",
    components: {
        Bar,
        Doughnut,
    },
    computed: {
        count() {
            return this.$store.getters.getCount;
        },
    },
    watch: {
        count() {
            this.data = this.getPricePerShop();
        },
    },
    data() {
        return {
            data: [],
        };
    },
    beforeMount() {
        this.data = this.getPricePerShop();
    },
};
</script>
