<template>
    <v-card>
        <v-toolbar density="compact" color="primary">
            <v-btn icon @click="$emit('close')">
                <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title>記録追加</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
                <v-btn variant="text" @click="onSave">
                    <v-icon start icon="mdi-content-save-outline"></v-icon
                    >保存</v-btn
                >
            </v-toolbar-items>
        </v-toolbar>

        <v-container>
            <v-combobox
                v-model="shop"
                :items="shop_list"
                item-title="name"
                density="compact"
                label="お店"
            ></v-combobox>
            <v-text-field
                v-model="price"
                label="金額"
                variant="outlined"
                clearable
                type="number"
            ></v-text-field>
            <v-text-field
                v-model="event_date"
                label="日付"
                variant="outlined"
                clearable
                type="date"
            ></v-text-field>
            <v-text-field
                v-model="comment"
                label="コメント"
                variant="outlined"
                clearable
            ></v-text-field>
        </v-container>
    </v-card>
</template>

<script>
export default {
    name: "NewEvent",
    props: {
        mode: Number,
        event: Object,
    },
    components: {},
    data: () => ({
        shop_list: [],
        shop: null,
        price: 0,
        event_date: "",
        comment: "",
    }),
    mounted() {
        this.shop_list = this.getShopList();
        if (this.mode == 1) {
            this.shop = {
                name: this.event.shop_name,
                color: this.event.event_color,
            };
            this.price = this.event.price;
            this.event_date = this.event.event_date;
            this.comment = this.event.comment;
        } else {
            this.event_date = this.formatDate(new Date());
        }
    },
    methods: {
        onSave() {
            if (this.mode == 0) {
                this.createEvent();
            } else {
                this.modEvent();
            }
            this.$emit("close");
        },
        async createEvent() {
            const d = new Date();
            const str = d.toLocaleString();
            const newEvent = {
                id: "",
                shop_name: this.shop.name,
                price: Number(this.price),
                event_date: this.event_date,
                comment: this.comment,
                event_color: this.shop.color,
            };

            newEvent.id = await this.createDigest(
                JSON.stringify(newEvent) + str
            );
            this.addEvent(newEvent);
            this.$store.dispatch("increment");
        },
        modEvent() {
            const event = {
                id: this.event.id,
                shop_name: this.shop.name,
                price: Number(this.price),
                event_date: this.event_date,
                comment: this.comment,
                event_color: this.shop.color,
            };

            this.updateEvent(event);
            this.$store.dispatch("increment");
        },
    },
};
</script>
