<template>
    <v-app>
        <v-app-bar app color="primary" density="compact">
            <v-app-bar-nav-icon
                variant="text"
                @click.stop="drawer = !drawer"
            ></v-app-bar-nav-icon>
            <v-toolbar-title class="ml4">居酒屋日記</v-toolbar-title>
            <v-spacer></v-spacer>

            <!-- <v-btn class="ml2" @click="$store.dispatch('increment')">
                <v-icon start icon="mdi-refresh"></v-icon
                ><span class=".d-none .d-sm-flex">一覧更新</span>
            </v-btn> -->
            <v-dialog v-model="new_event_dialog" width="80%">
                <template #activator="{ props }">
                    <v-btn
                        v-if="$vuetify.display.xs"
                        class="ml2"
                        v-bind="props"
                    >
                        <v-icon icon="mdi-pencil-plus-outline"></v-icon>
                    </v-btn>
                    <v-btn v-else class="ml2" v-bind="props">
                        <v-icon start icon="mdi-pencil-plus-outline"></v-icon
                        ><span>記録追加</span>
                    </v-btn>
                </template>
                <new-event :mode="0" @close="new_event_dialog = false" />
            </v-dialog>

            <v-dialog
                v-model="setting_dialog"
                fullscreen
                :scrim="false"
                transition="dialog-bottom-transition"
            >
                <template v-slot:activator="{ props }">
                    <v-btn
                        v-if="$vuetify.display.xs"
                        class="ml2"
                        v-bind="props"
                    >
                        <v-icon icon="mdi-cog-outline"></v-icon
                    ></v-btn>
                    <v-btn v-else class="ml2" v-bind="props">
                        <v-icon start icon="mdi-cog-outline"></v-icon
                        ><span>設定</span></v-btn
                    >
                </template>
                <app-settings @close="setting_dialog = false" />
            </v-dialog>
        </v-app-bar>
        <v-navigation-drawer v-model="drawer" location="left" temporary>
            <v-list>
                <v-list-subheader>メニュー</v-list-subheader>
                <v-divider></v-divider>
                <v-list-item
                    v-for="(item, i) in items"
                    :key="i"
                    :title="item.title"
                    @click="onClickMenu(item.value)"
                >
                </v-list-item
            ></v-list>
        </v-navigation-drawer>
        <v-main>
            <router-view />
        </v-main>
    </v-app>
</template>

<script>
import AppSettings from "./views/AppSettings.vue";
import NewEvent from "./views/NewEvent.vue";

export default {
    name: "App",
    components: {
        NewEvent,
        AppSettings,
    },
    data: () => ({
        drawer: false,
        new_event_dialog: false,
        setting_dialog: false,
        items: [
            {
                title: "ダッシュボード",
                value: 0,
            },
            {
                title: "タイムライン",
                value: 1,
            },
        ],
    }),
    mounted() {
        this.$router.replace({ path: "/" });
    },
    methods: {
        onClickMenu(v) {
            if (v == 0) {
                this.$router.replace({ path: "/" });
            } else if (v == 1) {
                this.$router.replace({ path: "/timeline" });
            }
        },
    },
};
</script>
