<script>
export default {
    install(Vue) {
        Vue.mixin({
            methods: {
                getEventData() {
                    return this.getData("izakaya_event");
                },
                setEventData(data) {
                    this.setData("izakaya_event", data);
                },
                addEvent(event) {
                    const data = this.getEventData();
                    if (!data.list) {
                        data.list = [];
                    }
                    data.list.push(event);
                    this.setEventData(data);
                },
                getEventList() {
                    const data = this.getEventData();
                    if (!data.list) {
                        return [];
                    }
                    data.list.sort((a, b) => {
                        const a_price = new Date(a.event_date);
                        const b_price = new Date(b.event_date);
                        // 戻りを逆にして降順にする
                        if (a_price > b_price) {
                            return -1;
                        } else if (a_price < b_price) {
                            return 1;
                        } else {
                            return 0;
                        }
                    });
                    return data.list;
                },
                getShopList() {
                    const data = this.getEventData();
                    if (!data.shop_list) {
                        return [];
                    }
                    return data.shop_list;
                },
                updateShopList(_shop_list) {
                    const data = this.getEventData();
                    data.shop_list = _shop_list;
                    this.setEventData(data);
                },
                deleteEvent(_event) {
                    const data = this.getEventData();
                    if (!data.list) {
                        return;
                    }

                    const filterData = data.list.filter((event) => {
                        return event.id != _event.id;
                    });
                    data.list = filterData;
                    this.setEventData(data);
                },
                // getPricePerShopPeriod(days) {
                //     const data = this.getEventList();
                // },
                getPricePerShop() {
                    const data = this.getEventList();
                    const group = data.reduce((result, current) => {
                        const el = result.find(
                            (d) => d.label === current.shop_name
                        );
                        if (el) {
                            el.data += current.price;
                        } else {
                            result.push({
                                label: current.shop_name,
                                data: current.price,
                            });
                        }
                        return result;
                    }, []);

                    group.sort((a, b) => {
                        if (a.data > b.data) {
                            return -1;
                        } else if (a.data < b.data) {
                            return 1;
                        } else {
                            return 0;
                        }
                    });

                    const colors = [
                        "#1976D2",
                        "#4CAF50",
                        "#FB8C00",
                        "#B00020",
                        "#03DAC6",
                    ];

                    group.forEach((d, i) => {
                        d.color =
                            i < colors.length
                                ? colors[i]
                                : colors[colors.length - 1];
                    });

                    const shopList = this.getShopList();
                    return group.filter((d) => {
                        const hitShop = shopList.filter(
                            (s) => s.name == d.label
                        );
                        return hitShop.length > 0 && hitShop[0].dispGraph;
                    });
                },
                updateEvent(_event) {
                    const data = this.getEventData();
                    if (!data.list) {
                        return;
                    }

                    const filterData = data.list.filter((event) => {
                        return event.id == _event.id;
                    });
                    if (filterData.length == 0) {
                        return;
                    }
                    filterData[0].shop_name = _event.shop_name;
                    filterData[0].price = Number(_event.price);
                    filterData[0].event_date = _event.event_date;
                    filterData[0].comment = _event.comment;
                    filterData[0].event_color = _event.event_color;

                    this.setEventData(data);
                },
                getData(key) {
                    var data = JSON.parse(localStorage.getItem(key));
                    if (data == null) {
                        return {};
                    }
                    return data;
                },
                setData(key, data) {
                    localStorage.setItem(key, JSON.stringify(data));
                },
            },
        });
    },
};
</script>
